import UiButton from "./UiButton.vue";
export default {
  components: { UiButton },
  props: {
    visible: {
      type: Boolean,
      default: false,
    },
    title: {
      type: String,
      default: "",
    },
  },
  computed: {
    dialogVisible: {
      get() {
        return this.visible;
      },
      set(val) {
        this.$emit("update:visible", val);
      },
    },
  },
  methods: {
    onConfirm() {
      this.$emit("confirm");
      this.dialogVisible = false;
    },
  },
};
